#Galerie3 .Around_hotel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

#Galerie3 .around_containt {
    width: 95%;
}


/* style for the title section */
#Galerie3 .around_containt .title_section {
     display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 11rem;
    margin-bottom: 10rem;
}

#Galerie3 .around_containt .title_section h1 {
    font-size: clamp(2rem, 5vw, 10vw);
    margin: .5rem 0;
}



#Galerie3 .around_containt .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Galerie3 .around_containt .title_section button {
      cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}


#Galerie3 .Around_hotel .places_list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fill, 23rem);
    gap: 1.5rem;
    row-gap: 5.5rem;
    padding-bottom: 3rem;
}

#Galerie3 .Around_hotel .single_place {
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
    height: 30rem;
    border-radius: 1rem;
    border: .7rem solid white;
}

#Galerie3 .Around_hotel .single_place:hover .details{
    height: 10rem;
}

#Galerie3 .Around_hotel .single_place:hover .details p {
    transform: translateY(0rem);
}

#Galerie3 .Around_hotel .single_place:hover .details button {
    transform: translateY(0rem);
}

#Galerie3 .Around_hotel .single_place img {
    position: absolute;
    object-fit: cover;
    cursor: pointer;
    width: 100%;
    height: 100%;
    z-index: 1;
    transition: .5s;
}

#Galerie3 .Around_hotel .single_place img:hover {
    scale: 1.05;
}

#Galerie3 .Around_hotel .details_box {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 1rem;
    width: 90%;
    z-index: 2;
    border-radius: 1rem;
    background-color: rgba(255, 255, 255, 0.347);
}

#Galerie3 .Around_hotel .details {
    text-align: center;
    overflow: hidden;
    margin: .5rem;
    border-radius: 1rem;
    padding: 1rem;
    transition: .3s;
    height: 3rem;
    background-color: white;


}

#Galerie3 .Around_hotel .details h3 {
    margin: 0;
    font-size: 1.5rem;
    color: rgb(53, 53, 53);

}

#Galerie3 .Around_hotel .details p {
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    transform: translateY(10rem);
    margin-top: .6rem;
    color: rgb(97, 97, 97);
    transition: .5s;



}

#Galerie3 .Around_hotel .details button {
    border: none;
    cursor: pointer;
    border-radius: 10rem;
    width: 8rem;
    height: 2rem;
    transform: translateY(10rem);
    transition: .51s;
    color: white;
    background-color: #3b61dd;

}

#Galerie3 .Around_hotel .details button:hover {
    width: 9rem;
}


#Galerie3 .Around_hotel .single_place .pupup {
    position: absolute;
    z-index: 4;
    top: 1rem;
    right: 1rem;
    padding: .15rem 2rem;
    border-radius: .3rem;
    margin: 0;
    color: white;
    background-color: rgb(80, 200, 80);
}


@media (max-width: 501px) {
    #Galerie3 .around_containt .title_section p {
    width: 90%;
    }

    #Galerie3 .Around_hotel .places_list {
    grid-template-columns: repeat(auto-fill, 90vw);
    row-gap: 1.5rem;
    }

    #Galerie3 .Around_hotel .single_place {
    height: 20rem;
    }

}


