#About5 .about_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 5rem;
    padding-bottom: 5rem;
}

#About5 .about_containt {
    display: flex;
    justify-content: center;
    gap: 2rem;
    width: 80%;
    background-color: white;
    border: .61rem solid white;
    border-radius: 1rem;
}

#About5 .description {
    width: 100%;
    padding: 3rem;
    margin-top: 0 !important;
}

#About5 .description h2 {
    font-size: clamp(1.3rem, 2.53vw, 5vw);
    color: rgb(57, 57, 57);
    margin-top: 0  ;
    color: rgb(63, 63, 63);

}

#About5 .description p {
    color: rgb(79, 79, 79);
    line-height: 1.7rem;
}

#About5 .description button {
    cursor: pointer;
    border: none;
    width: 10rem;
    height: 2.4rem;
    margin-top: 1rem;
    border-radius: 10rem;
    font-size: .91rem;
    background-color: #3b61dd;
    color: white;
}

#About5 .description i {
    margin-right: .4rem;
}

#About5 .video {
    position: relative;
    overflow: hidden;
    width: 100%;

}

#About5 .video img {
    object-fit: cover;
    filter: brightness(80%);
    width: 100%;
    height: 100%;
    transition: .5s;
    border-radius: 0 .51rem .51rem 0;
}

#About5 .video img:hover {
    scale: 1.051;
}

#About5 .video i {
    position: absolute;
    cursor: pointer;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 4rem;
    border-radius: 10rem;
    background-color: white;
    color: #3b61dd;
}


#About5 .about5_video_box {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.52);
    z-index: 99;
}

#About5 .about5_video {
    width: 70%;
    height: auto;
}

#About5 .about5_video .close_btn {
    display: flex;
    justify-content: end;
    gap: 1rem;
    color: white;
    padding-right: 2rem;
}

#About5 .about5_video .close_btn i {
    cursor: pointer;
    font-size: 1.72rem;
    margin-bottom: .5rem;
}

#About5 .about5_video video {
    width: 100%;
    height: 100%;
}



@media (max-width: 900px) {
    #About5 .about_containt {
        flex-direction: column-reverse;
        gap: .5rem;
    }

    #About5 .video img {
        height: 25rem;
        border-radius: .5rem;
    }

    #About5 .description {
        width: auto;
        padding: 1rem;
        padding-top: 0;
        padding-bottom: 2rem;
    }

    #About5 .description h2 {
        margin-top: 0;
        font-size: clamp(1.4rem, 3.8vw, 5vw);
    }

    #About5 .about5_video_box {
        background-color: black;
    }
    #About5 .about5_video {
        width: 100%;
        height: auto;
        background-color: black;
    }

}

@media (max-width: 501px) {
    #About5 .about_containt {
        width: 90%;
    }

    #About5 .video img {
        height: 15rem;
    }

    #About5 .description {
        padding-left: 0;
        padding-right: 0;
    }
}
