#Faq_3 .faq_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
}

#Faq_3 .faq_containt {
    padding: 0 7vw;
}



#Faq_3 .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-top: 10rem;
   margin-bottom: 10rem;

}

#Faq_3 .title_section h1 {
   font-size: clamp(1.82rem, 3.2vw, 10vw) !important;
   margin: 1rem 0;
   color: rgb(63, 63, 63);
}



#Faq_3 .title_section p {
   text-align: center;
   font-size: clamp(1rem, 1.13vw, 4vw);
   line-height: 1.7rem;
   width: 100%;
   margin: .5rem;
   color: rgb(100, 100, 100);
}

#Faq_3 .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}




/* //////////////////////////////////////////////// */

#Faq_3 .questions_container {
    display: flex;
    gap: 1rem;
}


#Faq_3 .side_bar_box {
    width: 30%;
}


#Faq_3 .side_bar {
    width: 100%;
}


#Faq_3 .side_bar_containt {
    border-radius: 1rem;
    padding: 0.1rem 2rem;
    background-color: white;

}

#Faq_3 .side_bar_containt a {
    text-decoration: none;
}

#Faq_3 .side_bar_containt h5 {
    cursor: pointer;
    color: rgb(69, 69, 69);
    font-size: 1rem;
}



#Faq_3 .single_list_containt {
    border-radius: 1rem;
    overflow: hidden;
    background-color: white;
    color: rgb(69, 83, 104);
    border: 1px solid rgba(0, 0, 255, 0.109);
}

#Faq_3 .single_list_containt h5 {
    margin: 0;
    padding: 1rem 2rem;
    cursor: pointer;
    font-size: 1rem;
    border-bottom: 1px solid rgba(0, 0, 255, 0.115);
}






#Faq_3 .single_list_containt p {
    margin: 0;
    overflow: hidden;
    max-height: 0;
    padding: 0;
    padding: 0rem 2rem;
    border: none;
    
    transition: .51s;
    height: 0;
}


#Faq_3 .single_list_containt p.active {
    max-height: 14rem;
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(0, 0, 255, 0.115);
    
    height: 10rem;
}




#Faq_3 .question_list {
    width: 70%;
}


@media (max-width: 800px) {
    #Faq_3 .questions_container {
        flex-direction: column;
    }

    #Faq_3 .side_bar_box {
        width: 100%;
    }

    #Faq_3 .question_list {
        width: 100%;
    }
}


@media (max-width: 501px) {
    
#Faq_3 .title_section p {
    text-align: center;
    width: 115%;
 }
}