#Room_details .room_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

#Room_details .room_containt {
    width: 85%;
}









/* style for the title section */
#Room_details .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 11rem;
    margin-bottom: 10rem;
}

#Room_details .title_section h1 {
    font-size: clamp(2rem, 4vw, 10vw);
    margin: .5rem 0;
    color: rgb(60, 60, 60);
}



#Room_details .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Room_details .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}





#Room_details .room_containt_2 {
    display: flex;
    gap: 2rem;
}

#Room_details .sections {
    width: 70%;
}

#Room_details .images_box {
    border-radius: 1rem;
    border: .81rem solid white;
    background-color: white;
}

#Room_details .big_img {
    width: 100%;
    height: 30rem;
}

#Room_details .sections img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .5rem;
    cursor: pointer;
}

#Room_details .sections img:hover {
    filter: brightness(60%);
}


#Room_details .other_room {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 1rem;
}

#Room_details .other_room img {
    /* width: 25%; */
    margin-top: .5rem;
}







#Room_details .description {
    margin-top: 5rem;
}

#Room_details .description h3 {
    font-size: clamp(1.3rem, 1.72vw, 4vw);
    color: rgb(51, 51, 51);
}

#Room_details .description p {
    color: rgb(56, 56, 56);
    line-height: 1.75rem;
}


#Room_details .location {
    margin-top: 5rem;
}

#Room_details .location h3 {
    font-size: clamp(1.3rem, 1.72vw, 4vw);
    color: rgb(51, 51, 51);
}






#Room_details .services {
    margin-top: 5rem;
}

#Room_details .services h3 {
    font-size: clamp(1.3rem, 1.72vw, 4vw);
    color: rgb(51, 51, 51);
}



#Room_details  .offer_list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    justify-content: center;
    gap: 1rem;
}

#Room_details  .single_offer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    padding: 1.5rem .5rem;
    border-radius: .5rem;
    transition: .5s;
    background-color: white;
}

#Room_details  .single_offer:hover {
    scale: 1.1;
}

#Room_details  .single_offer i {
    font-size: 2rem;
    color: rgb(108, 106, 106);
}

#Room_details  .single_offer p {
    margin-bottom: 0;
    color: rgb(94, 94, 94);
}

#Room_details .checkout_mobile {
    display: none;
}

@media (max-width: 1000px) {
    #Room_details  .side_bar {
        display: none;
    }

    
    #Room_details .checkout_mobile {
    display: block;
    margin-top: 6rem;
    }

    

    #Room_details  .sections {
        width: 100%;
    }
}


@media (max-width: 800px) {
    #Room_details .room_containt {
        width: 90%;
    }

    #Room_details .title_section p {
        width: 100%;
    }
    
}


@media (max-width: 501px) {
    #Room_details .offer_list {
        grid-template-columns: 1fr 1fr;
        text-align: center;
        gap: .5rem;
    }

    #Room_details  .location img {
        height: 22rem;
    }

    #Room_details  .other_room {
        gap: .5rem;
    }

    #Room_details .big_img {
        width: 100%;
        height: 18rem;
    }
}






#Room_details .calandar_box {
    margin-top: 5rem;
    width: 100%;
}

#Room_details .calandar_box h3 {
    font-size: clamp(1.3rem, 1.72vw, 4vw);
    color: rgb(51, 51, 51);
}












/* ////////////////////////////////////////////////////////////// */
#Room_details .side_bar {
    width: 30%;
    background-color: transparent;

}

#Room_details .side_bar_containt {
    padding: 0rem;
    background-color: transparent;
}





#Room_details .size {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: .5rem;
    background-color: white;
    padding: 0 1rem;
    color: rgb(78, 78, 78);

}

#Room_details .size > div {
    display: flex;
    align-items: center;
    gap: .5rem;
    padding: 0 .5rem;
}





#Room_details .price {
    text-align: center;
    background-color: white;
    border-radius: .5rem;
    padding: 1rem 0;
    margin-top: 1rem;
}

#Room_details .price p {
    color: rgb(89, 89, 89);
    margin: 0;
}

#Room_details .price span {
    font-size:  2rem;
    font-weight: bold;
    color: rgb(75, 167, 75)
}




#Room_details form {
    background-color: white;
    border-radius: .5rem;
    margin-top: 1rem;
    padding: 2rem 1rem;
     /* border: 1px solid gray; */
}



#Room_details form input {
    width: 98.5%;
    height: 2.8rem;
    margin-top: .5rem;
    margin-bottom: 1rem;
    padding: 0;
    border-radius: .5rem;
    border: none;
    /* border: 1px solid gray; */
    font-size: 1rem;
    color: rgb(84, 84, 84);
    background-color: rgba(173, 216, 230, 0.364);
}



#Room_details form input::placeholder {
    padding-left: 1rem;
    color: red;
}

#Room_details form .button_box {
    margin-top: 1rem;
}


    #Room_details form button {
    padding: .6rem 2rem;
    width: 100%;
    text-decoration: none;
    border-radius: .5rem;
    border: none;
    background-color: #3b61dd;
    color: white;
    cursor: pointer;  
}

/* /////////////////////////////////////////////////////////////////////// */
















#Room_details .calandar_box h3 {
    margin-bottom: .5rem;
}


#Room_details .calandar_box > div {
     display: grid;
     grid-template-columns: 1fr 1fr;
     gap: 1rem;
}

#Room_details .calandar_box > div > div h4 {
    margin-bottom: .4rem;
    color: gray;
}



#Room_details .Calandar_ {
    width: 100%;
    border: none;

}

#Room_details .Calandar_ .react-calendar__navigation {
    background-color: #3b61dd;
    color: white !important;
}

#Room_details .Calandar_ .react-calendar__navigation button {
    color: white;
}

#Room_details .react-calendar__month-view__weekdays__weekday abbr{
   text-decoration: none;
}





#Room_details .review {
    margin-top: 5rem;
}

#Room_details .review h3 {
    font-size: clamp(1.3rem, 1.72vw, 4vw);
    color: rgb(51, 51, 51);

}


#Room_details .review form{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

#Room_details .review form textarea {
    width: 99%;
    height: 10rem;
    border: none;
    border-radius: .5rem;
}

#Room_details .review form textarea:focus {
    outline: none;
}

#Room_details .review form input {
    height: 3rem;
    width: 99%;
    border: none;
    border-radius: .5rem;
}

#Room_details .review form input:focus {
    outline: none;
}


#Room_details .review form button {
    width: 10rem;
    height: 2.5rem;
    border-radius: .5rem;
    border: none;
    background-color: #3b61dd;
    color: white;
    cursor: pointer;
}



@media (max-width: 600px) {
    #Room_details .calandar_box > div {
        grid-template-columns: 1fr;
        gap: 2rem;
   }
}






