#Team3_restau .team_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 4rem;
}

#Team3_restau .team_containt {
    width: 90%;
}




#Team3_restau .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-bottom: 3rem !important;
   padding-top: 5rem;
   margin-top: 0 !important;
}

#Team3_restau .title_section h1 {
   font-size: clamp(1.7rem, 2.6vw, 10vw);
   font-size: clamp(2rem, 3vw, 10vw) !important;
   margin: .5rem 0;
   color: black;
   color: rgb(63, 63, 63);
}



#Team3_restau .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}


#Team3_restau .team_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 18rem);
    justify-content: center;
    align-items: center;
    gap: 1rem;
    row-gap: 3rem;
}

#Team3_restau .single_team {
    padding-bottom: 1rem;
    border-radius: 1rem;
    border: .7rem solid white;
    background-color: white;
}

#Team3_restau .single_team .img_box {
    overflow: hidden;
   width: 100%;
   height: 20rem;
}

#Team3_restau .single_team .img_box img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: .51rem;
    transition: .6s;
}

#Team3_restau .single_team:hover img {
    scale: 1.1;
}

#Team3_restau .description {
    margin-top: 2rem;
}

#Team3_restau .description h3{
    text-align: center;
    font-size: clamp(1.4rem, 1.7vw, 4vw);
    margin: 0;
    margin-top: .5rem;
    color: rgb(47, 47, 47);
}

#Team3_restau .description p {
    text-align: center;
    margin: .5rem 0rem;
    color: rgb(77, 77, 77);
}

#Team3_restau .icons_section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    font-size: 1.1rem;
    padding-top: .6rem;
    color: rgb(85, 85, 85);
}

#Team3_restau .icons_section i {
    cursor: pointer;
}

#Team3_restau .icons_section i:hover {
    scale: 1.4;
}


@media (max-width: 800px) {
    #Team3_restau .title_section p {
        width: 100% !important;
     }
}

@media (max-width: 501px) {
    #Team3_restau .team_list {
        grid-template-columns: repeat(auto-fill, 80vw);
        row-gap: 1rem;
    }

}

