#About_restau .about_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 5rem;
    padding-bottom: 5rem;
}

#About_restau .about_containt {
    display: flex;
    justify-content: center;
    /* gap: 2rem; */
    width: 80%;
    background-color: white;
    border: .61rem solid white;
    border-radius: 1rem;
}

#About_restau .description {
    width: 100%;
    padding: 3rem;
    margin: 0;
}

#About_restau .description h2 {
    font-size: clamp(1.3rem, 2.53vw, 5vw);
    color: rgb(57, 57, 57);
    margin-top: 0;
}

#About_restau .description p {
    color: rgb(79, 79, 79);
    line-height: 1.7rem;
}

#About_restau .description button {
    cursor: pointer;
    border: none;
    width: 10rem;
    height: 2.4rem;
    margin-top: 1rem;
    border-radius: 10rem;
    font-size: .91rem;
    background-color: #3b61dd;
    color: white;
}

#About_restau .description i {
    margin-right: .4rem;
}

#About_restau .image_box {
    position: relative;
    overflow: hidden;
    width: 130%;
}

#About_restau .image_box img {
    object-fit: cover;
    filter: brightness(80%);
    width: 100%;
    height: 100%;
    transition: .5s;
    border-radius: 0 .51rem .51rem 0;
}

#About_restau .image_box img:hover {
    scale: 1.051;
}






@media (max-width: 900px) {
    #About_restau .about_containt {
        flex-direction: column;
    }

    #About_restau .image_box {
        position: relative;
        overflow: hidden;
        width: 100%;
    }


    #About_restau .image_box img {
        height: 25rem;
    }

    #About_restau .description {
        width: auto;
        padding: 1rem 0;
        padding-top: 0;
        padding-top: 2rem;
    }

    #About_restau .description h2 {
        margin-top: 0;
        font-size: clamp(1.4rem, 3.8vw, 5vw);
    }


    #About_restau .image_box {
        border-radius: .51rem ;

    }
   
}

@media (max-width: 501px) {
    #About_restau .about_containt {
        width: 90%;
    }

    #About_restau .image_box img {
        height: 15rem;

    }
}
