#Galerie1 .Welcom_section {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100vw;
    padding: 3rem 0;
    padding-bottom: 0;
    padding-top: 8rem;
    background-color: rgb(233, 245, 253);
}

#Galerie1 .welcom_containt {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-bottom: 2rem;
    padding-top: 0 !important;
}





/* Title section */
#Galerie1 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 3rem !important;
    margin-top: 0 !important;
}

#Galerie1 .title_section h1 {
    font-size: clamp(1.7rem, 3vw, 10vw);
    margin: .5rem 0;
    margin-top: 0;
    padding: 0 .5rem;
    color: rgb(63, 63, 63);
}



#Galerie1 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Galerie1 .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}







/* Sliders container */
#Galerie1 .welcom_containt .slides_img_container {
    display: flex;
    overflow-x: auto;
    width: 90%;
    gap: 2rem;
    padding-left: 15rem;
    padding-right: 15rem;
    padding: 2rem 15rem;
}

#Galerie1 .welcom_containt .single_slide {
    position: relative;
    min-width: 40vw;
    transition: .45s;
}

#Galerie1 .welcom_containt .single_slide:hover {
    scale: 1.051;
}

#Galerie1 .welcom_containt .single_slide .position {
    position: absolute;
    top: -.5rem;
    right: 1rem;
    padding: .4rem .8rem;
    border-radius: .3rem;
    background-color: orange;
}

#Galerie1 .welcom_containt .single_slide .bi-fullscreen {
    position: absolute;
    cursor: pointer;
    font-size: 4rem;
    transform: translate(-50%, -50%);
    transition: .25s;
    top: 50%;
    left: 50%;
    opacity: 0;
    color: rgba(255, 255, 255, 0.738);
} 

#Galerie1 .welcom_containt .single_slide .bi-fullscreen:hover {
    color: orangered;
}

#Galerie1 .welcom_containt .single_slide:hover .bi-fullscreen {
    opacity: 1;
}


#Galerie1 .welcom_containt .single_slide img {
    object-fit: cover;
     width: 100%;
     height: 25rem;
     transition: 1s;
}



#Galerie1 .welcom_containt .controls_btns {
    display: flex;
    justify-content: end;
    align-items: center;
    transform: translateY(-8rem);
    width: 90%;
    height: 5rem;
}

#Galerie1 .welcom_containt .controls_btns .counter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 2.5rem;
    border-radius: 10rem;
    font-size: 1.1rem;
    background-color: white;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.607);
}

#Galerie1 .welcom_containt .controls_btns .btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.5rem;
    width: 6rem;
    padding: 0 1rem;
    border-radius: 10rem;
    font-size: 1.5rem;
    background-color: white;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.607);

}

#Galerie1 .welcom_containt .controls_btns i {
    cursor: pointer;
    display: block !important;
}

@media (max-width: 1000px) {
#Galerie1 .title_section p {
   width: 95% !important;   
}
}


@media (max-width: 600px) {
#Galerie1 .welcom_containt .slides_img_container {
   gap: 1rem;
}

    #Galerie1 .welcom_containt .single_slide {
        min-width: 80vw;
    }
}




#Galerie1 .welcom_containt .hotel_stats {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 80%;
    padding: .81rem;
    gap: .81rem;
    border-radius: .71rem;
    background-color: white;
   
}

#Galerie1 .welcom_containt .hotel_stats h4 {
    font-size: clamp(1.2rem, 2.53vw, 10vw);
    margin: .5rem;
    margin-bottom: 0;
}

#Galerie1 .welcom_containt .hotel_stats h5 {
    margin-top: .4rem;
    font-size: 1.1rem;
    color: gray;

}

#Galerie1 .welcom_containt .hotel_stats .single_stat {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0rem 0;
    background-color: rgb(233, 247, 255);
    border-radius: .71rem;
}


@media (max-width: 600px) {
    #Galerie1 .welcom_containt .hotel_stats {
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 501px) {
    #Galerie1 .welcom_containt .single_slide img {
        height: 18rem;
   }

   
#Galerie1 .welcom_containt .controls_btns {
    display: none;
    justify-content: center;
    transform: translateY(-3rem);
}

#Galerie1 .welcom_containt .controls_btns .btns {
    height: 2rem;
}




#Galerie1 .welcom_containt .hotel_stats {
    margin-top: 5rem;
}

#Galerie1 .welcom_containt .hotel_stats h5 {
    margin-bottom: .8rem;
}

}