#Product1 .best_room_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
}

#Product1 .best_room_containt {
    display: flex;
    flex-direction: column;
    width: 100%;
}





/* Title section */
#Product1 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 4rem;
    margin-top: 0;
}

#Product1 .title_section h1 {
    font-size: clamp(1.7rem, 3vw, 10vw);
    margin: .5rem 0;
    margin-top: 0;
    color: rgb(63, 63, 63);
}



#Product1 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Product1 .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}



    





/* styles for the rooms */
#Product1 .best_room_container .room_list {
    display: flex;
    align-items: center;
    overflow-x: auto;
    gap: 1rem;
    padding-bottom: 2rem;
    padding-left: 5rem;
    padding-right: 5rem;
}





 /* Width of the scrollbar */
#Product1 .room_list::-webkit-scrollbar {
    width: 10px;
}

/* Color of the track */
#Product1 .room_list::-webkit-scrollbar-track {
    background: #f1f1f100; 
}

/* Color of the scrollbar handle */
#Product1 .room_list::-webkit-scrollbar-thumb {
    background: #88888800;
}

 /* Color of the scrollbar handle on hover */
#Product1 .room_list::-webkit-scrollbar-thumb:hover {
    background: #5555555e;
  }
  
  
  
  
  
  /* Single Item */
#Product1 .room_list .single_list {
    min-width: 20rem;
    width: 25vw;
    padding: .7rem;
    border-radius: 1rem;
    background-color: white;
}

#Product1 .room_list .image_box {
    overflow: hidden;
    width: 100%;
    height: 13rem;
    
}

#Product1 .room_list .single_list img {
    cursor: pointer;
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: .7rem;
    transition: .36s;
}

#Product1 .room_list .single_list img:hover {
    scale: 1.081;
}




#Product1 .room_details .room_size {
    display: flex;
    align-items: center;
    gap: 1rem;
    background-color: rgba(173, 216, 230, 0.308);
    margin-top: .5rem;
    margin-bottom: .6rem;
    border-radius: .41rem;
    padding: 0 .6rem;
    height: 2rem;
}

#Product1 .room_details .size_1 {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .61rem;
}

#Product1 .room_details .size_1 p {
    margin: .5rem 0;
    color: rgb(67, 67, 67);
}

#Product1 .room_details .size_2 p {
    transform: translateY(-.2rem);
}

#Product1 .room_details h3 {
    font-size: 1.4rem;
    margin: 0rem 0;
    margin-bottom: .5rem;
    color: rgb(63, 63, 63);
}

#Product1 .room_details .desc {
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-box-orient: vertical;
    margin: 0;
    font-size: 1rem;
    /* font-size: clamp(.9rem, 1.4vw, 1.081rem); */
    line-height: 1.5rem;
    color: rgb(131, 129, 129);
}

#Product1 .room_details .room_price {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#Product1 .room_details .room_price p {
    color: rgb(89, 89, 89);
}

#Product1 .room_details .room_price span {
    font-weight: bold;
    color: rgba(0, 128, 0, 0.781);
    font-size: 1.4rem;
}

#Product1 .room_details .room_price button {
    border: none;
    cursor: pointer;
    width: 7rem;
    height: 2rem;
    border-radius: 10rem;
    font-size: .81rem;
    transition: .123s;
    color: white;
    background-color: #3b61dd;
}

#Product1 .room_details .room_price button:hover {
    scale: 1.05;
}

#Product1 .best_room_container .controls_box {
    display: flex;
    justify-content: center;
    padding: 0 6vw;
}

#Product1 .best_room_container .controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.5rem;
    padding: 0 6vw;
    width: 15rem;
    font-size: 1.5rem;
    border-radius: 10rem;
    background-color: white;
    box-shadow: 0 0 2rem #3b61dd31;
}

#Product1 .best_room_container .controls i {
    cursor: pointer;
}




@media (max-width: 1000px) {
    #Product1 .title_section p {
       width: 95%;   
    }
    }


@media (max-width: 501px) {
    #Product1  .title_section button {
        width: 60vw;
        padding: .61rem 0;
        font-size: .91rem;
        border: .5rem solid white;
    }

    #Product1 .title_section p {
        font-size: clamp(.81rem, 1.17vw, 4vw);
        line-height: 1.5rem;
    }

    #Product1 .best_room_container .room_list {
        gap: .8rem;
    }


    #Product1 .room_list .single_list {
        min-width: 76vw;
    }

    
#Product1 .room_list .image_box {
    width: 100%;
    height: 10rem;
}

#Product1 .room_details h3 {
    font-size: 5.5vw;
}


#Product1 .room_details .desc {
   line-height: 1.4rem;
}



#Product1 .room_details .room_price p span{
    display: block;
 }

 
#Product1 .room_details .room_price button {
    height: auto;
    width: 8rem;
    padding: .6rem 0;

}

#Product1 .best_room_container .room_list {
    padding: 1rem;
}

}

