    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 11rem;
    margin-bottom: 7rem;
}

#Restau .title_section h1 {
    font-size: clamp(2rem, 4vw, 10vw);
    margin: .5rem 0;
    padding: 0 .5rem;
    color: rgb(60, 60, 60);
}



#Restau .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Restau .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}

@media (max-width: 800px) {
    #Restau .title_section p {
        width: 93%;
    }
}