#Search_page .search_container {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    inset: 0;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.864);
    z-index: 11;
}

#Search_page .search_containt {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    background-color: rgba(255, 255, 255, 0.349);
    border-radius: 1rem;
}


#Search_page .close_btn {
    position: absolute;
    top: -2.2rem;
    right: 0.5rem;   
    color: white;
}

#Search_page .close_btn i{
    font-size: 1.5rem;
}




#Search_page  form {
    width: 40vw;
    min-width: 20rem;
    display: flex;
    flex-direction: column;
    padding: 1.5rem;
    border-radius: .71rem;
    gap: .8rem;
    border: 1px solid rgb(181, 181, 181);
    background-color: white;
}

#Search_page  form .adult_children {
    display: flex;
    width: 100%;
    gap: 1rem;
}

#Search_page  form .adult_children > div{
   width: 100%;
}

#Search_page  form .adult_children input {
    width: 100%;
}

#Search_page  form label {
    color: rgb(63, 63, 63);
    font-size: 1.1rem;
    color: black;
}

#Search_page  form input {
    border: none;
    width: 100%;
    height: 2.5rem;
    font-size: 1rem;
    border-radius: .5rem;
    margin-top: .8rem;
    border: 1px solid rgb(178, 178, 178);
    background-color: rgba(173, 216, 230, 0.318);

}

#Search_page  form input:focus {
    outline: none;
}

#Search_page  form .button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    text-decoration: none;
    border: none;
    cursor: pointer;
    margin-top: 1rem;
    height: 2.5rem;
    border-radius: .5rem;
    font-size: 1.1rem;
    color: white;
    background-color: #3b61dd;
}


@media (max-width: 501px) {
    #Search_page .search_containt {
    padding: .51rem;
}

    #Search_page  form {
    width: 80vw;
    min-width: 2rem;
    padding: 1.4rem .8rem;
    
}

}
