.contact_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
}

.contact_containt {
    width: 80%;
}






#Contact_hotel .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 11rem;
    margin-bottom: 10rem;
}

#Contact_hotel .title_section h1 {
    font-size: clamp(1.5rem, 4vw, 10vw);
    margin: .5rem 0;
}



#Contact_hotel .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 80%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Contact_hotel .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}





.Contact {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    justify-content: space-between;
    border: .7rem solid white;
    background-color: white;
    gap: 1rem;
    border-radius: 1rem;
}

.Contact > div {
    width: 100%;
    border-radius: 1rem;
    background-color: rgb(233, 245, 253);

    text-align: center;
    padding: 1rem 0;
}

.Contact > div i {
    font-size: 2.5rem;
    color: gray;
}

.Contact > div h5 {
    font-size: 1rem;
    color: rgb(74, 74, 74);
    margin-bottom: 0;
    margin-top: .5rem;
}

.Contact > div p {
    color: rgb(87, 87, 87);
    padding: 0 .5rem;
}





















#Contact_hotel .Contact_title {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 8rem;
    margin-bottom: 10rem;
}

#Contact_hotel .Contact_title h1 {
    font-size: clamp(1.5rem, 2.83vw, 10vw);
    margin: .5rem 0;
}



#Contact_hotel .Contact_title p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 80%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Contact_hotel .Contact_title button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}

.contact_box {
    display: flex;
    gap: 2rem;
    border: .9rem solid white;
    overflow: hidden;
    border-radius: 1rem;
    background-color: white;
}

.contact_box > div {
    width: 100%;
    border: 1px solid rgba(128, 128, 128, 0.095);
    /* height: 20rem; */
}

.contact_box > div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .5rem 0 0 .5rem;
}

.contact_box form {
    width: 100%;
    padding: 1rem 0;
    padding-top: 0;
    /* border: 1px solid gray; */
}

.contact_box form h2 {
    margin-top: 0;
}

.contact_box form label {
    color: gray;
}

.contact_box form input {
    width: 99%;
    height: 2.5rem;
    border: none;
    border-radius: .5rem;
    display: block;
    margin-top: .5rem;
    margin-bottom: 1rem;
    background-color: rgb(233, 245, 253);

}

.contact_box form input:focus {
    outline: none;
}


.contact_box form textarea {
    display: block;
    width: 99%;
    margin-top: .5rem;
    height: 10rem;
    border: none;
    border-radius: .5rem;
    background-color: rgb(233, 245, 253);

}

.contact_box form textarea:focus {
    outline: none;
}

.contact_box form button {
    width: 10rem;
    height: 2.5rem;
    border: none;
    background-color: #3b61dd;
    color: white;
    cursor: pointer;
    border-radius: .5rem;
    margin-top: 2rem;
}


@media (max-width: 1000px) {
    .contact_box > div {
        display: none;
    }
}


@media (max-width: 800px) {
    .contact_containt {
        width: 90%;
    }

    
    #Contact_hotel .title_section p {
        width: 105% !important;
    }


    #Contact_hotel .Contact_title p {
        width: 105%;
    }
}


@media (max-width: 501px) {
    
.Contact {
    grid-template-columns: 1fr;
}

#Contact_hotel .title_section button {
    width: 70vw;
}



#Contact_hotel .Contact_title button {
    width: 70vw;
}
}

