#Cart_hotel .cart_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

#Cart_hotel .cart_containt  {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
}











#Cart_hotel .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 10rem;
    margin-bottom: 5rem;
    margin-top: 8rem;

}

#Cart_hotel .title_section h1 {
   font-size: clamp(2rem, 4vw, 10vw);
   margin: 1rem 0;
   color: rgb(63, 63, 63);
}


#Cart_hotel .title_section p {
   text-align: center;
   font-size: clamp(1rem, 1.13vw, 4vw);
   line-height: 1.7rem;
   width: 90% !important;
   margin: .5rem;
   color: rgb(100, 100, 100);
}

#Cart_hotel .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}


.empty_cart {
    width: 100%;
}

.empty_cart h1 {
    font-size: clamp(2rem, 3vw, 5vw);
    text-align: center;
    color: rgb(67, 67, 67);
}






#Cart_hotel .cart_containt table.product {
    border: 1px solid red;
    width: 70vw;
    min-width: 43rem;
    border: 2rem solid white;
    background-color: white;
    border-radius: 1rem;
    
}

#Cart_hotel .cart_containt table.product tr:first-child td {
    font-weight: bold;
    padding-bottom: 3rem;
}

#Cart_hotel .cart_containt table.product img {
    width: 6rem;
    height: 6rem;
    object-fit: cover;
    border-radius: 1rem;
}

#Cart_hotel .cart_containt table.product p {
    margin: .4rem 0;
    color: gray;
}

#Cart_hotel .cart_containt table.product h4 {
    margin: .4rem 0 .8rem 0;
    color: rgb(80, 80, 80);
}


#Cart_hotel .cart_containt tr {
    border: 1px solid black;
}


#Cart_hotel .cart_containt tr td {
    /* border: 1px solid black; */
    padding: .71rem;
}

#Cart_hotel .cart_containt table.product .img_box {
    display: flex;
    align-items: center;
    gap: 1rem;
    width: auto;
}

#Cart_hotel .cart_containt table.product .img_box i {
    color: red;
    cursor: pointer;
}

#Cart_hotel .cart_containt table.product input {
   width: 4rem;
   height: 2rem;
   border: 1px solid lightblue;
   /* border: none; */
   border-radius: .5rem;
   text-align: center;
   background-color: rgba(173, 216, 230, 0.306);
}

#Cart_hotel .cart_containt table.product input:focus {
    outline: none;
}


#Cart_hotel .mobile_cart_list {
    display: none;
}


#Cart_hotel .Clear_btn {
    display: flex;
    flex-direction: end;
    justify-content: center;
    margin-top: 2rem;
}

#Cart_hotel .Clear_btn button {
    width: 100%;
    min-width: 6rem;
    /* background-color: rgba(148, 6, 6, 0.866); */
    border: none;
    height: 2.5rem;
    /* color: white; */
    font-weight: bold;
    border-radius: .5rem;
    cursor: pointer;
    border: 1px solid red;
    color: red;
    background-color: rgba(173, 216, 230, 0.273);
    background-color: white;
}


