    padding: 0rem 2rem;
    border-radius: 1rem;
} */

#Cart_hotel .cart_containt .total_container {
    display: flex;
    justify-content: end;
    width: 70vw;
    margin-top: 2rem;
}

#Cart_hotel .cart_containt table.total {
    width: 25rem;
    border-radius: 1rem;
    background-color: white;
    padding: 1.5rem;
}

#Cart_hotel .cart_containt table.total h2 {
    margin-top: 0;
    color: rgb(58, 58, 58);
}

#Cart_hotel .cart_containt table.total h3 {
    margin: 0;
    color: rgb(58, 58, 58);
}

#Cart_hotel .cart_containt table.total h4 {
    margin: 0;
    color: rgb(58, 58, 58);
}

#Cart_hotel .cart_containt table.total td {
    padding: .4rem 0;
}

#Cart_hotel .cart_containt table.total .submit_box {
   display: flex;
   justify-content: center;
   margin-top: 1rem;
}

#Cart_hotel .cart_containt table.total .Link {
    width: 100%;
}

#Cart_hotel .cart_containt table.total button {
    width: 100%;
    height: 2.2rem;
    border: none;
    background-color: #3b61dd;
    border-radius: 10rem;
    color: white;
    font-weight: bold;
    cursor: pointer;
}




@media (max-width: 650px) {
    #Cart_hotel .cart_containt table.product {
        display: none;
    }
    

/* //////////////////////////////////////////// */
#Cart_hotel .mobile_cart_list {
    /* border: 1px solid red; */
    width: 100%;
    margin-top: 4rem;
    display: block;
}

#Cart_hotel .mobile_cart_list img {
    width: 7rem;
    height: 4rem;
    border-radius: .41rem;
    object-fit: cover;
    margin-bottom: 1rem;
}


#Cart_hotel .mobile_cart_list .single_item {
    border: 2rem solid white;
    background-color: white;
    margin: 1rem 0;
    border-radius: 1rem;
}

#Cart_hotel .mobile_cart_list .single_item > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(128, 128, 128, 0.178);
}

#Cart_hotel .mobile_cart_list .single_item h4 {
    margin: .5rem;
    color: rgb(59, 59, 59);
}


#Cart_hotel .mobile_cart_list .single_item p {
    margin: .4rem;
    color: rgb(79, 79, 79);
}

#Cart_hotel .mobile_cart_list input {
    width: 4rem;
    height: 1.52rem;
    text-align: center;
    border-radius: .4rem;
    border: 1px solid lightblue;
    background-color: rgba(173, 216, 230, 0.323);
}


#Cart_hotel .mobile_cart_list input:focus {
    outline: none;}


    #Cart_hotel .Clear_btn button {
        width: 10rem;
    }

    #Cart_hotel .Clear_btn.mobile {
        display: flex;
        justify-content: end;
    }


    #Cart_hotel .cart_containt .total_container {
        width: 90vw;
    }

    #Cart_hotel .cart_containt table.total {
        width: 100%;
        padding: 1.5rem;
    }

}








@media (max-width: 800px) {
    #Cart_hotel .title_section p {
        width: 100%;
     }
}


@media (max-width: 400px) {
    #Cart_hotel .mobile_cart_list .single_item {
        border:.51rem solid white;
        padding: 1rem 0;
    }
    
}
