#Checkout .checkout_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

#Checkout .checkout_containt {
    width: 80%;
}








#Checkout .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-top: 10rem;
   margin-bottom: 10rem;

}

#Checkout .title_section h1 {
   font-size: clamp(1.82rem, 5.2vw, 10vw) !important;
   margin: 1rem 0;
   color: rgb(63, 63, 63);
}



#Checkout .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}



#Checkout .checkout_body {
    display: flex;
    gap: 5vw;
    background-color: white;
    padding: 2rem;
}



#Checkout .checkout_body > div:first-child {
    width: 100%;
}

#Checkout .first_last_name {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    
}

#Checkout .first_last_name div {
    width: 100%;
}

#Checkout .checkout_body > div:first-child label {
    /* display: block; */
    color: rgb(64, 64, 64);
}

#Checkout .checkout_body > div:first-child input {
    width: 100%;
    padding: 0;
    height: 3rem;
    border-radius: .5rem;
    margin: .4rem 0;
    margin-bottom: 1.2rem;
    background-color: rgba(173, 216, 230, 0.266);
    border: none;
}

#Checkout .checkout_body > div:first-child .Street {
    margin-bottom: .3rem;
}

#Checkout .checkout_body > div:first-child input:focus {
    outline: none;
}



#Checkout .checkout_body > div:last-child {
    width: 100%;
}

#Checkout .checkout_body table {
    width: 100%;
}

#Checkout .checkout_body table td {
    border-bottom: 1px solid rgba(128, 128, 128, 0.132);
    padding: .8rem 0;
}

#Checkout .checkout_body table td h4 {
    margin: 0;
    margin-bottom:  .4rem;
    color: rgb(56, 56, 56);
}

#Checkout .checkout_body table td p {
    margin: 0;
    margin-bottom:  .1rem;
    color: rgb(94, 93, 93);
}

#Checkout .checkout_body table td strong {
    margin: 0;
}








#Checkout .checkout_body .payment {
    /* border: 1px solid red; */
    padding-bottom: 2rem;
    margin-top: 3rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.158);

}

#Checkout .checkout_body .payment p {
    margin: 0;
}

#Checkout .checkout_body .payment label {
    color: rgb(95, 95, 95);
}

#Checkout .checkout_body .payment div {
    background-color: rgba(173, 216, 230, 0.252);
    padding: 1rem;
    border-radius: .5rem;
    margin: .5rem 0;
    color: rgb(65, 64, 64);
}


#Checkout .checkout_body .privacy p {
    color: gray;
    margin-bottom: 2rem;
}


#Checkout .checkout_body .submit {
    display: flex;
    justify-content: end;
}

#Checkout .checkout_body .submit button {
    width: 8rem;
    height: 2.5rem;
    border: none;
    border-radius: 10rem;
    background-color: #3b61dd;
    color: white;
    cursor: pointer;
}

#Checkout .checkout_body .total_section h3 {
    margin: 0;
}

#Checkout .checkout_body .total_section td{
    padding: .4rem;
}


@media (max-width: 1100px) {
    #Checkout .checkout_body {
        flex-direction: column;
        padding: 1rem;
    }
}

@media (max-width: 501px) {
    #Checkout .checkout_containt {
        width: 90%;
    }

    #Checkout .first_last_name {
        flex-direction: column;
        gap: 0;
    }
}
