#Footer3 .footer_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 5rem;
    background-color: rgba(10, 26, 39, 0.90);
    color: white;
}

#Footer3 .footer_containt {
    display: flex;
    justify-content: center;
    gap: 2rem;
    width: 90%;
    padding-top: 4rem;
    padding-bottom: 4rem;
}

#Footer3 .block {
    width: 25%;
}

#Footer3 .block h2 {
    margin-bottom: 3rem;
}

#Footer3 .block1 p {
    color: rgb(209, 209, 209);
    line-height: 1.7rem;
    margin-bottom: 2rem;
}

#Footer3 .block1 .icon_section {
    display: flex;
    gap: 1rem;
    font-size: 1.2rem;
}

#Footer3 .block1 .icon_section i:hover {
    cursor: pointer;
    color: rgb(255, 149, 111);
}

#Footer3 .block1, .block4 {
    width: 35%;
}

#Footer3 .block2 p:hover, .block3 p:hover {
    cursor: pointer;
    color: rgb(255, 149, 111);
}

#Footer3 .block4 .images_section {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: .5rem;
}

#Footer3 .block4 img {
    object-fit: cover;
    cursor: pointer;
    width: 44%;
    height: 6rem;
    border-radius: .41rem;
}







#Footer3 .image_bg {
    position: absolute;
    object-fit: cover;
    height: 100%;
    width: 100%;
    z-index: -1;
}


@media (max-width: 1050px) {
   #Footer3 .footer_containt {
       display: grid; 
       grid-template-columns: 1fr 1fr;
   }

   #Footer3 .block {
    width: 100%;
}
}

@media (max-width: 600px) {
    #Footer3 .footer_containt {
        display: grid; 
        grid-template-columns: 1fr;
    }
 
    #Footer3 .block {
        text-align: center;
    } 

    #Footer3 .block1 .icon_section {
        justify-content: center;
    }

    #Footer3 .block4 .images_section {
        justify-content: center;
    }


 }