
#Hero_1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    
}

#hero1_container {
    width: 80%;
}


#Hero_1 .header_background {
    position: absolute;
    inset: 0;
    z-index: -1;
    background-color: black;
}



#Hero_1 .img_background_box {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    inset: 0;
    z-index: -1;
}


#Hero_1 .image_background_containt {
    animation: hero ease-in-out 30s infinite;
    height: 100%;
}


#Hero_1 .hero_background {
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: brightness(30%);
}

#Hero_1 .hide_img {
    animation: hide_img ease 2s forwards;
}

@keyframes hide_img {
    0% {opacity: 1;}
    100% {opacity: 0;}
}

#Hero_1 .show_img {
    animation: show_img ease 2s forwards;
}

@keyframes show_img {
    0% {opacity: 0;}
    100% {opacity: 1;}
}


@keyframes hero {
    0% {
        scale: 1;
    }

    50% {
        scale: 1.3;
    }
    
    100% {
        scale: 1;
    }
}


#Hero_1 .hero_containt {
    display: flex;
    align-items: center;
    width: 100%;
}



#Hero_1 .hero_containt .details_section {
    width: 100%;
}


#Hero_1 .hero_containt  .stars i {
    margin: 0 .4rem;
    font-size: 1.2rem;
    color: gold;
} 

#Hero_1 .hero_containt h1 {
    font-size: clamp(1.8rem, 4.7vw, 5vw);
    margin: 0;
}

#Hero_1 .hero_containt .details {
    font-weight: bold;
    width: 80%;
}


#Hero_1 .hero_containt form {
    display: flex;
    flex-direction: column;
    width: 30vw;
    padding: 1.5rem;
    border-radius: .71rem;
    gap: .8rem;
    background-color: rgba(255, 255, 255, 0.76);
    border: 1px solid rgb(181, 181, 181);
}

#Hero_1 .hero_containt form .adult_children {
    display: flex;
    gap: 1rem;
}

#Hero_1 .hero_containt form .adult_children input {
    width: 100%;
}

#Hero_1 .hero_containt form label {
    color: rgb(63, 63, 63);
    font-size: 1.1rem;
    color: black;
}

#Hero_1 .hero_containt form input {
    border: none;
    width: 100%;
    height: 2.5rem;
    font-size: 1rem;
    border-radius: .5rem;
    margin-top: .8rem;
    background-color: rgb(227, 227, 227);
    background-color: white;
    border: 1px solid rgb(178, 178, 178);
}

#Hero_1 .hero_containt form input:focus {
    outline: none;
}

#Hero_1 .hero_containt form .button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    text-decoration: none;
    margin-top: 1rem;
    height: 2.5rem;
    border-radius: .5rem;
    font-size: 1.1rem;
    color: white;
    background-color: #3b61dd;
}





@media (max-width: 1000px) {
    #Hero_1 {
        height: 130vh;
    }
    
    #hero1_container {
        height: 120vh;
        display: flex;
        align-items: end;
    }
    
    #Hero_1 .hero_containt {
        flex-direction: column; 
    }


    #Hero_1 .hero_containt h1 {
        font-size: clamp( 2.7rem, 6vw, 20vw);
    }

    #Hero_1 .hero_containt .details {
        margin-bottom: 2rem;
        width: 100%;
    }
    
    #Hero_1 .hero_containt form {
        gap: 0.2rem;
        width: 70vw;
        margin-top: 6rem ;
        background-color: rgb(237, 237, 237);
        border: 1px solid rgb(225, 225, 225);
    }


}

   @media (max-width: 501px) {
    #Hero_1 {
        height: 100vh;
    }

    #hero1_container {
        width: 90%;
        height: 95vh;
    }

    #Hero_1 .hero_containt form {
       width: 75vw;
    }
    
    #Hero_1 .hero_containt form input {
    height: 1.72rem;
    margin-top: .1rem;
    }

    #Hero_1 .hero_containt form label {
    font-size: .9rem;
    }

    #Hero_1 .hero_containt h1 {
        font-size: clamp(1.4rem, 9vw, 9vw);
    }

    #Hero_1 .hero_containt .details {
    width: 100%;
    font-size: clamp(.8rem, 1.5vw, 6vw);
} 
    
}


       