#service_4 .service_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 5rem;
    padding-bottom: 5rem;
}

#service_4 .service_containt {
    width: 80%;
}


/* Title */

#service_4 .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-bottom: 5rem;

}

#service_4 .title_section h1 {
   font-size: clamp(1.7rem, 3vw, 10vw);
   margin: 1rem 0;
   color: rgb(63, 63, 63);

}



#service_4 .title_section p {
   text-align: center;
   font-size: clamp(1rem, 1.13vw, 4vw);
   line-height: 1.7rem;
   width: 90%;
   margin: .5rem;
   color: rgb(100, 100, 100);
}






#service_4 .offer_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 15rem);
    justify-content: center;
    gap: 2rem;
}

#service_4 .single_offer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    padding: 2rem 0;
    border-radius: 1rem;
    border: .7rem solid white;
    background-color: white;
    
}


#service_4 .single_offer i {
    font-size: 2.5rem;
    color: rgb(75, 75, 75);
}

#service_4 .single_offer p {
    margin-bottom: 0;
}


@media (max-width: 800px) {
    #service_4 .service_containt {
        width: 90%;
    }
}


@media (max-width: 501px) {
    #service_4 .service_containt {
        width: 100%;
    }

    #service_4 .single_offer {
        padding: 1rem 0;
    }

    #service_4 .offer_list {
        padding: 0 1rem;
        grid-template-columns: 1fr 1fr;
        gap: .5rem;
    }
}