#Room_page .best_room_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
}

#Room_page .best_room_containt {
    display: flex;
    flex-direction: column;
    width: 85%;
}






/* style for the title section */
#Room_page .best_room_containt .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 11rem;
    margin-bottom: 10rem;
}

#Room_page .best_room_containt .title_section h1 {
    font-size: clamp(1.92rem, 4vw, 10vw);
    margin: .5rem 0;
    color: rgb(60, 60, 60);
}



#Room_page .best_room_containt .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Room_page  .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}


/* styles for the rooms */
#Room_page .best_room_container .room_list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
}



/* Single Item */
#Room_page .room_list .single_list {
    padding: .7rem;
    border-radius: 1rem;
    background-color: white;
}

#Room_page .room_list .image_box {
    overflow: hidden;
    width: 100%;
    height: 20rem;
    
}

#Room_page .room_list .single_list img {
    cursor: pointer;
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: .7rem;
    transition: .36s;
}

#Room_page .room_list .single_list img:hover {
    scale: 1.081;
}



#Room_page .room_details .room_size {
    display: flex;
    align-items: center;
    gap: 1rem;
    background-color: rgba(173, 216, 230, 0.308);
    margin-top: .8rem;
    margin-bottom: .6rem;
    border-radius: .41rem;
    padding: 0 .6rem;
    height: 2rem;
}

#Room_page .room_details .size_1 {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .61rem;
}

#Room_page .room_details .size_1 p {
    margin: .5rem 0;
    color: rgb(67, 67, 67);
}

#Room_page .room_details .size_2 p {
    transform: translateY(-.2rem);
}

#Room_page .room_details h3 {
    font-size: 1.4rem;
    margin: 0rem 0;
    margin-bottom: .5rem;
    color: rgb(60, 60, 60);
}

#Room_page .room_details .desc {
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-box-orient: vertical;
    margin: 0;
    font-size: 1rem;
    line-height: 1.4rem;
    color: rgb(82, 82, 82);
}

#Room_page .room_details .room_price {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#Room_page .room_details .room_price p {
    color: rgb(89, 89, 89);
}

#Room_page .room_details .room_price span {
    font-weight: bold;
    color: rgba(0, 128, 0, 0.781);
    font-size: 1.4rem;
}

#Room_page .room_details .room_price button {
    border: none;
    cursor: pointer;
    width: 7rem;
    height: 2.3rem;
    border-radius: 10rem;
    font-size: .81rem;
    transition: .123s;
    color: white;
    background-color: #3b61dd;
}

#Room_page .room_details .room_price button:hover {
    scale: 1.05;
}







@media (max-width: 1438px) {
    #Room_page .best_room_container .room_list {
        grid-template-columns: 1fr 1fr; 
    }

    #Room_page .room_list .image_box {
        height: 18rem;
        
    }
    
}

@media (max-width: 750px) {
    #Room_page .best_room_container .room_list {
        grid-template-columns: 1fr; 
    }

    #Room_page .best_room_containt .title_section p {
        width: 100%;
    }
}


@media (max-width: 501px) {
    #Room_page  .title_section button {
        width: 60vw ;
        padding: .61rem 0;
        font-size: .91rem;
        border: .5rem solid white;
    }

    #Room_page .best_room_containt .title_section p {
        width: 110%;
    }

  

    #Room_page .room_list .single_list {
        min-width: 85vw;
    }

    
#Room_page .room_list .image_box {
    width: 100%;
    height: 14rem;
}



#Room_page .room_details .desc {
   line-height: 1.4rem;
}



#Room_page .room_details .room_price p span{
    display: block;
 }

 
#Room_page .room_details .room_price button {
    height: auto;
    padding: .6rem 0;

}

#Room_page .best_room_container .room_list {
    padding: 1rem;
}


}

