#Galerie_ .galerie_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
}

#Galerie_ .galerie_containt {
    width: 80%;
}


/* style for the title section */
#Galerie_ .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-top: 11rem !important;
   margin-bottom: 10rem;
}

#Galerie_ .title_section h1 {
   font-size: clamp(1.5rem, 5vw, 10vw);
   margin: .5rem 0;
   color: rgb(60, 60, 60);
}



#Galerie_ .title_section p {
   text-align: center;
   font-size: clamp(1rem, 1.17vw, 4vw);
   line-height: 1.7rem;
   width: 70%;
   margin: 0;
   color: rgb(100, 100, 100);
}

#Galerie_ .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}


/* grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr)); */


    #Galerie_ .galerie_list {
         padding: 1rem;
         max-width: 100%;
         margin: 0 auto;
         /* background-color: #f2f2f2; */
         display: grid;
         grid-gap: 1rem;
         grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
         grid-auto-rows: 250px;
         grid-auto-flow: dense;
    }


    #Galerie_ .v-stretch {
        grid-row: span 2;
    }

    #Galerie_ .h-stretch {
        grid-column: span 2;
    }

    #Galerie_ .big-stretch {
        grid-row: span 2;
        grid-column: span 2;
    }

    #Galerie_ .galerie_list > div {
        width: 100%;
        height: 100%;
        border-radius: .71rem;
        overflow: hidden;
        overflow: hidden;
        position: relative;
    }

    #Galerie_ .galerie_list > div img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: .5s;
        cursor: pointer;
    }

    #Galerie_ .galerie_list > div img:hover {
        scale: 1.05;
    }

    #Galerie_ .galerie_list > div p {
        position: absolute;
        z-index: 10;
        top: -.51rem;
        right: .5rem;
        background-color: orangered;
        color: white;
        font-weight: bold;
        padding: .2rem 1rem;
        border-radius: .4rem;
        opacity: 0;
        transition: .3s;
    }

    #Galerie_ .galerie_list > div:hover p {
        opacity: 1;
    }


    #Galerie_ .Active_img_box {
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        inset: 0;
        z-index: 10;
        background-color: rgba(0, 0, 0, 0.872);
    }
    
    #Galerie_ .Active_img_box .Active_img {
        width: 80%;
        height: 90%;
        position: relative;
        transform: translateY(-1rem);
    }

    #Galerie_ .Active_img_box .Active_img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    #Galerie_ .Active_img_box .Active_img p {
        position: absolute;
        z-index: 11;
        right: 1rem;
        top: 1.82rem;
        background-color: orangered;
        color: white;
        font-weight: bold;
        padding: .2rem 1rem;
        border-radius: .4rem;
    }

    #Galerie_ .Active_img_box .Active_img .close {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;
    }

    #Galerie_ .Active_img_box .Active_img .close h3 {
        margin: .4rem;
        color: white;
    }

    #Galerie_ .Active_img_box .Active_img i {
        color: white;
        cursor: pointer;
    }


    #Galerie_ .Active_img > i {
        position: absolute;
        top: 50%;
        background-color: black;
        width: 3rem;
        height: 3rem;
        border: 1px solid black;
        background-color: rgba(0, 0, 0, 0.44);
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10rem;
    }

    #Galerie_ .Active_img > i:first-of-type {
        left: 1rem;
    }

    #Galerie_ .Active_img > i:last-of-type {
        right: 1rem;
    }

    


    @media (max-width: 700px) {
        #Galerie_ .v-stretch {
            grid-row: span 1;
        }
    
        #Galerie_ .h-stretch {
            grid-column: span 1;
        }
    
        #Galerie_ .big-stretch {
            grid-row: span 1;
            grid-column: span 1;
        }

        #Galerie_ .galerie_containt {
            width: 90%;
        }

        #Galerie_ .title_section p {
            width: 100%;
         }


    }
    
    
    @media (max-width: 501px) {
        #Galerie_ .galerie_list {
            grid-template-columns: repeat(auto-fit, minmax(11rem, 1fr));   
       }

       #Galerie_ .title_section button {
        width: 70vw ;
     }

         
    #Galerie_ .Active_img_box .Active_img {
        width: 100%;
        
    }

    #Galerie_ .Active_img_box .Active_img .close {
        padding: 0 2rem;
    }
        
    }