#Menu .menu_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 3rem;
}

#Menu .menu_containt {
    width: 100%;
}






#Menu .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 5rem;
}

#Menu .title_section h1 {
    font-size: clamp(1.5rem, 3vw, 10vw);
    margin: .5rem 0;
}



#Menu .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}











    #Menu .menu_list {
        overflow-x: auto;
        display: flex;
        gap: 1rem;
        padding: 2rem 5rem;

    }
    
    #Menu .single_menu {
        border: .7rem solid white;
        border-radius: 1rem;
        background-color: white;
    }
    
    #Menu .single_menu .img_box {
        width: 100%;
        height: 15rem;
    }
    
    #Menu .single_menu .img_box img {
        width: 23rem;
        height: 100%;
        object-fit: cover;
        border-radius: .5rem;
    }

    #Menu .single_menu h3 {
        font-size: clamp(1rem, 1.5vw, 3vw);
        color: rgb(54, 54, 54);
    }

    #Menu .single_menu p {
        color: rgb(68, 68, 68);
    }
    
    #Menu .single_menu div:last-of-type {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
    }
    #Menu .single_menu div:last-of-type p {
        color: gray;
    }

    #Menu .single_menu div:last-of-type p span {
        font-size: 1.4rem;
        font-weight: bold;
        color: rgb(68, 176, 68);
    }

    #Menu .single_menu div:last-of-type button {
        width: 6rem;
        height: 1.82rem;
        border-radius: .5rem;
        border: none;
        cursor: pointer;
        background-color: #3b61dda2;
        background-color: #3b61dde3;
        color: white;

    }

    @media (max-width: 800px) {
        #Menu #Menu .title_section p {
            width: 93% !important;
        }
    }


    @media (max-width: 600px) {
        #Menu .menu_list {
            padding: 2rem ;
            gap: 1rem;
        }

        #Menu .single_menu .img_box img {
            width: 76vw;
        }
    }


    @media (max-width: 501px) {
        #Menu .menu_list {
            padding: 1rem;
        }
    }

    
