#Blog3 .blog_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-bottom: 5rem;
    /* padding-top: 5rem; */
}

#Blog3 .blog_containt {
    width: 100%;
}












/* Title section */
#Blog3 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 4rem !important;
    margin-top: 2rem !important;
}

#Blog3 .title_section h1 {
    font-size: clamp(1.7rem, 3vw, 10vw) !important;
    margin: .5rem 0;
    margin-top: 0;
    color: rgb(63, 63, 63);
}



#Blog3 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}

#Blog3 .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}











/* BLog list */

#Blog3 .blog_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 26vw);
    justify-content: center;
    align-items: center;
    gap: 1rem;
    row-gap: 4rem;
}

#Blog3 .single_blog {
    border-radius: .71rem;
    border: .6rem solid white;
    background-color: white;

}

#Blog3 .img_box {
    width: 100%;
    height: 12rem;
    cursor: pointer;
}

#Blog3 .img_box img {
    width: 100%;
    height: 100%;
    border-radius: .5rem;
}

#Blog3 .blog_details {
    padding: 0 .5rem;
}


#Blog3 .info {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-top: .8rem;
    border-radius: 10rem;
    background-color: rgb(233, 245, 253);
}

#Blog3 .single_info {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .3rem;
}

#Blog3 .single_info p {
    font-size: clamp(.86rem, 1vw, 2rem) !important;
    margin: .5rem 0;
}

#Blog3 .blog_details h3 {
    margin: 1rem 0;
    color: rgb(81, 81, 81);
    font-size: clamp( 1rem, 1.3vw, 2vw);
}

#Blog3 .blog_details .text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    margin-top: 0;
    font-size: clamp( .91rem, 1vw, 2vw);
    color: gray;
    line-height: 1.6rem;

}

@media (max-width: 1000px) {
   #Blog3 .title_section p {
      width: 92% !important;   
   }
}

@media (max-width: 1050px) {
    #Blog3 .blog_list {
        grid-template-columns: repeat(auto-fill, 40vw);
    }
}

@media (max-width: 700px) {
    #Blog3 .blog_list {
        grid-template-columns: repeat(auto-fill, 25rem);
    }
}

@media (max-width: 501px) {
    #Blog3 .blog_list {
        grid-template-columns: repeat(auto-fill, 90vw);
    }
}