

 #Feedback_1 .Feedback {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    /* padding-top: 10rem; */
}

#Feedback_1 .feedback_containt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 45rem;
}











#Feedback_1 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 4rem !important;
    margin-top: 2rem !important;
}

#Feedback_1 .title_section h1 {
    font-size: clamp(1.7rem, 3vw, 10vw) !important;
    margin: .5rem 0;
    padding: 0 .5rem;
    margin-top: 0;
    color: rgb(63, 63, 63);
}



#Feedback_1 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.17vw, 4vw);
    line-height: 1.7rem;
    width: 70%;
    margin: 0;
    color: rgb(100, 100, 100);
}













#Feedback_1 .feedback_list_box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}


#Feedback_1 .feedback_list_box:hover .bi-chevron-left
#Feedback_1 .feedback_list_box:hover .bi-chevron-right {
    opacity: 1 ;
    display: none;
}




#Feedback_1 .Feedback .feedbacks_list {
    display: flex;
    width: 50vw;
}

#Feedback_1 .Feedback .single_feedback_box {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 50vw;
    transition: .5s;
}



#Feedback_1 .Feedback .single_feedback {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
}

#Feedback_1 .Feedback .single_feedback img {
    width: 5rem;
    height: 5rem;
    border-radius: 10rem;
    object-fit: cover;
}

#Feedback_1 .Feedback .single_feedback h3 {
    font-size: 1.5rem;
    color: rgb(87, 87, 87);
    margin: .6rem 0;
    margin-bottom: 0;
}

#Feedback_1 .Feedback .single_feedback .position {
    color: gray;
    margin-top: .5rem;
}

#Feedback_1 .Feedback .single_feedback .location {
    margin-top: 0.5rem;
    margin-bottom: 2rem;
    color: gray;
}

#Feedback_1 .Feedback .description {
   text-align: center;
   background-color: white;
   padding: 1rem;
   border-radius: .71rem;
   margin-top: 0rem !important;
}

#Feedback_1 .Feedback .description p {
    font-size: clamp(.91rem, 1.15vw, 10vw);
    color: rgba(46, 98, 144, 0.872);
    line-height: 1.7rem;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
}

#Feedback_1 .Feedback .description i {
    font-size: 1.1rem;
    margin: 0 .2rem;
    color: #F1C40F  ;
}

#Feedback_1 .Feedback .control_btns {
    width: 43vw;
    height: 23rem !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transform: translateY(-18rem);
    transition: .25s;
}



#Feedback_1 .Feedback .bi-chevron-left,
#Feedback_1 .Feedback .bi-chevron-right {
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 10;
   background-color: #3b61dd;
   width: 3.5vw;
   height: 3.5vw;
   min-width: 2rem;
   min-height: 2rem;
   font-size: clamp(1.5rem, 2vw, 5vw);
   border-radius: 10rem;
   color: white;
   cursor: pointer;
   transform: translateY(-1rem);
   opacity: .7;
   transition: .2s;
}


#Feedback_1 .Feedback .bi-chevron-left:hover,
#Feedback_1 .Feedback .bi-chevron-right:hover {
   opacity: 1;
}




#Feedback_1 .Feedback .bi-chevron-left {
    left: 28vw;
}

#Feedback_1 .Feedback .bi-chevron-right {
    right: 28vw;

}

@media (max-width: 1000px) {
    #Feedback_1 .title_section p {
       width: 92% !important;   
    }
    }

@media (max-width: 800px) {
    #Feedback_1 .Feedback .description {
        padding: 1rem .5rem;
     }


    #Feedback_1 .Feedback .feedbacks_list {
        width: 100vw;
    }

    #Feedback_1 .Feedback .single_feedback_box {
        min-width: 100vw;

    }

    #Feedback_1 .Feedback .control_btns {
        width: 90vw;
        opacity: .7;
    }
}
 
