#About_2 .about_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 10rem;
}

#About_2 .about_containt {
    width: 80%;
}


#About_2 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 10rem;

}

#About_2 .title_section h1 {
   font-size: clamp(2rem, 4vw, 10vw);
   margin: 1rem 0;
   color: rgb(63, 63, 63);
}


#About_2 .title_section p {
   text-align: center;
   font-size: clamp(1rem, 1.13vw, 4vw);
   line-height: 1.7rem;
   width: 100% !important;
   margin: .5rem;
   color: rgb(100, 100, 100);
}

#About_2 .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}


#About_2 .section2 {
    position: relative;
    display: flex;
    gap: 1rem;
}


#About_2 .image_box {
    width: 38vw;
    height: 22rem;
    border: .71rem solid white;
    border-radius: .7rem;
}


#About_2 .image_box img {
    object-fit: cover;
    width: 25vw;
    height: 100%;
}


#About_2 .description {
    position: absolute;
    top: 5.6rem;
    right: 0;
    width: 67%;
}


#About_2 .description h3 {
    margin-bottom: 0;
    color: rgb(66, 66, 66);
}



#About_2 .description .job {
    margin-top: .3rem;
}


#About_2 .description .user_box i {
    display: none;
}



#About_2 .text_box {
    position: relative;
    padding: 3rem 4rem;
    border-radius: 1rem;
    font-size: clamp(1rem, 1.25vw, 3vw);
    line-height: 2rem;
    background-color: white;
    color: rgb(94, 94, 94);
}

#About_2 .text_box p {
    margin: 0;
}

#About_2 .text_box i {
    position: absolute;
    top: .50rem;
    left: .50rem;
    font-size: 2.5rem;
}




@media (max-width: 1100px) {
    #About_2 .section2 {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #About_2 .about_containt {
        width:95%;
    }


    #About_2 .title_section p {
         width: 100% !important;
    }


    #About_2 .description {
        position: relative;
        top: 0rem !important;
        width: 100%;
        background-color: white;
        margin-top: 0 !important;
    }

    #About_2 .text_box {
        padding: 1rem 1rem 0 1rem;
    }

    #About_2 .image_box {
        border: none; 
        width: 100%;
    }

    #About_2 .image_box img {
        object-fit: contain;
        width: 100%;
    }

    #About_2 .text_box i {
        display: none;   
    }



    #About_2 .description .user_box {
        display: flex;
        align-items: center;
        gap: .5rem;
    }

    #About_2 .description .user_box i {
        font-size: 3.5rem;
        display: block;
        color: gray;
    }
    
}



